<template>
	
    <div class="container">
		
        <br>
	    <div class="row">
	      <div class="col-md-12">
	         <b-breadcrumb :items="items"/>
	      </div>
	    </div>
	    <br>
        
        <div class="row">
            
            <div class="col-md-12">
                
                <b-form-group horizontal label="Filtrar" :label-cols="1">
                
                    <b-form-input v-model="filter" placeholder="Buscar..." />
                
                </b-form-group>

           </div>

        </div>

        <div class="row">
                        
            <div class="col-md-6">

            </div>

            <div class="col-md-6 text-md-right">
                <b-button :disabled="!sortBy" @click="sortBy = null">Limpiar busquedas</b-button>
            </div>

        </div>
     	
        <br>

		<div class="row">
			<div class="col-md-12">
                <b-table striped hover show-empty
                    :items="userCourse"
                    :fields="rows"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    @filtered="onFiltered">
                    
                    <template slot="material" slot-scope="row">
                        <b-btn size="sm" @click.stop="getTheory(row.item,row.index,$event.target)">Material</b-btn>
                    </template>

                    <template slot="training" slot-scope="data">
                        {{data.value.name}}
                    </template>

                     <template slot="user" slot-scope="data">
                        {{data.value.name}}
                    </template>

                </b-table>

			</div>

		</div>

        <div class="row">
            <div class="col-md-8">
                <b-pagination :total-rows="userCourse.length" :per-page="perPage" v-model="currentPage" />
            </div>
        </div>       
        
        <b-modal id="modal1" @hide="resetModal" ok-only>
            <h4 class="my-1 py-1" slot="modal-header">Index: {{ modalDetails.index }}</h4>
            <pre>{{ modalDetails.data }}</pre>
        </b-modal>

	</div>
</template>

<script>

import { getUserCourseListPage, getTheoryListPage } from '../endpoints';

export default {

  computed: {
  },

  data () {
    name: 'userCourse'
        
        return {
            sysId: this.$auth.user().id,
            items: 
            [
                {
                    text: 'Home',
                    href: '/',
                }, 
                {
                    text: 'Tus cursos',
                    active: true
                }
            ],



            rows: {
                id:             { label: 'Id', sortable: true },
                training:       { label: 'Nombre curso', sortable: true  },
                user:           { label: 'Nombre usuario', sortable: true  },
                correctAnswers: { label: 'Preguntas correctas', sortable: true },
                wrongAnswers:   { label: 'Preguntas Incorrectas', sortable: true  },
                created_at:     { label: 'Fecha de creaci√≥n' },
                updated_at:     { label: 'Fecha de actualizaci√≥n' },
                material:       { label: 'Material' }
            },
            userCourse: [],
            currentPage: 1,
            perPage: 5,
            totalRows: 0,
            sortBy: null,
            sortDesc: false,
            filter: null,
            listLoading: false,
            modalDetails: { index:'', data:'' }
        }
    },
    methods: {
    
        getTraining() {
            
            this.listLoading = true;
            let params = { userId: this.sysId };
            getUserCourseListPage(params).then((res) => {
                this.userCourse = res.data;
                this.listLoading = false;
            });
        },

        getTheory(item, index, button) {
            
            this.listLoading = true;
            let params = { trainingId: item.training.id };
            getTheoryListPage(params).then((res) => {
                this.modalDetails.data = JSON.stringify(res.data, null, 2);
                this.listLoading = false;
                this.$root.$emit('bv::show::modal','modal1', button);
            });
        },

        resetModal() {
          this.modalDetails.data = '';
          this.modalDetails.index = '';
        },
    },
    mounted() {
        this.getTraining();
    }
}
</script>


