<template>
  <b-navbar toggleable="md" type="dark" variant="dark">
    <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
    <b-navbar-brand href="/">Online Learning</b-navbar-brand>
    <b-collapse is-nav id="nav_collapse">
      
      <!-- Right aligned nav items -->      
      <b-navbar-nav class="ml-auto">        
        <b-nav-item >{{sysUserName}}</b-nav-item>
        <b-nav-item @click="logout">Logout</b-nav-item>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>
<script>
export default {
  name: 'header',
  data () {
    return {
      sysUserName: this.$auth.user().name
    }
  },
  methods: {      
    logout: function () {
      //this.sysUserName = '',
      this.$auth.logout({
          makeRequest: true,
          redirect: '/login'
      });
    }
  }
}
</script>



