<template>
  <div class="container">
    
    <br>
      <div class="row">
        <div class="col-md-12">
           <b-breadcrumb :items="items"/>
        </div>
      </div>
    <br>

    <div class="row">
            
            <div class="col-md-12">
                
                <b-form-group horizontal label="Filtrar" :label-cols="1">
                
                    <b-form-input v-model="filter" placeholder="Buscar..." />
                
                </b-form-group>

           </div>

        </div>

      <div class="row">
        
            <div class="col-md-6">
          <b-btn variant="success" @click="showAdd">Agregar</b-btn>
        </div>
                
            <div class="col-md-6 text-md-right">
                <b-button :disabled="!sortBy" @click="sortBy = null">Limpiar busquedas</b-button>
            </div>

      </div>

      <b-table striped hover show-empty
           :items="typeUser"
           :fields="fields"
           :current-page="currentPage"
           :per-page="perPage"
           :filter="filter"
           :sort-by.sync="sortBy"
           :sort-desc.sync="sortDesc"
           @filtered="onFiltered">
        
        <template slot="actions" slot-scope="row">
            
            <!-- We use click.stop here to prevent a 'row-clicked' event from also happening -->
            <b-btn size="sm" @click.stop="handleEdit(row.item,row.index)">
              <icon name="pencil"></icon>Editar
            </b-btn>
          
          <b-btn size="sm" @click.stop="handleDel(row.item,row.index)" variant="danger">
            <icon name="trash"></icon>Eliminar</span>
          </b-btn>

        </template>

      </b-table>
      <div class="row">
        <div class="col-md-12 text-center">
            <b-pagination :total-rows="typeUser.length" :per-page="perPage" v-model="currentPage" />
        </div>
      </div>
    <b-modal hide-footer ref="addTypeUser" title="Add Type User">
          
      <b-form @submit="onSubmit">
          
          <b-form-group   id="typeUser"
                        label-for="TypeUser">
            <b-form-input   id="TypeUser"
                            type="text" v-model="form.type" required
                            placeholder="Ingrese el tipo de usuario">
            </b-form-input>

          </b-form-group>

          <b-button type="submit" class="btn-lg btn-block" variant="primary">Submit</b-button>
      
      </b-form>

    </b-modal>

    <b-modal hide-footer ref="updateTypeUser" title="Edit Type User">
          
      <b-form @submit="onEditSubmit">
          
          <b-form-group   id="editTypeUser"
                          label-for="EditTypeUser">
            <b-form-input   id="EditTypeUser"
                            type="text" v-model="editForm.type" required
                            placeholder="Ingrese el tipo de usuario">
            </b-form-input>

          </b-form-group>
          
          <b-button type="submit" class="btn-lg btn-block" variant="primary">Submit</b-button>
      
      </b-form>

    </b-modal>

  </div>
</template>

<script>

  
  
import { getTypeUserListPage, addTypeUser, removeTypeUser, editTypeUser } from '../endpoints';
//import Errors from '../common/js/Errors';

export default {


  computed: {
  },

  data () {
    name: 'typeUser'
    return {
       
      items: 
      [
          {
              text: 'Home',
              href: '/',
          }, 
          {
              text: 'Tipo de usuario',
              active: true
          }
      ],
        
      form: {
        type: ''
      },

      fields: {
        id:       { label: 'Id', sortable: true },
        type:     { label: 'Tipo', sortable: true  },
        created_at: { label: 'Fecha de creaci√≥n' },
        updated_at: { label: 'Fecha de actualizaci√≥n' },
        actions:    { label: 'Acciones' }
       },

      pageOptions: [{text:5,value:5},{text:10,value:10},{text:15,value:15}],
    
      typeUser: [],
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      sortBy: null,
      sortDesc: false,
      filter: null,
      listLoading: false,
      addFormVisible: false,
      addLoading: false,
      editFormVisible: false,
      editLoading: false,
      addForm: {},
      editForm: {
        id: 0,
        type: ''
      }
    }
  },
  methods: {
    getTypeUsers() {
    /*let para = {
      name: this.filters.type,
      sortBy: this.sortBy,
    };*/
    this.listLoading = true;
    getTypeUserListPage().then((res) => {
      //this.total = res.data.total;
      this.typeUser = res.data;
      this.listLoading = false;
      console.log(this.typeUser.length);
    });
    if(this.listLoading == false)
      this.totalRows = typeUser.length;
  },
  onSubmit(evt) {
      this.addLoading = true;
        addTypeUser(this.form).then((response) => {
            this.addLoading = false;
            this.$notify({
              title: 'Success',
              text: response.data.message,
              type: 'success'
            });
            //this.$refs['addForm'].resetFields(); // maybe not necessary
            this.form.type = '';
            this.$refs.addTypeUser.hide();
            this.addFormVisible = false;
            this.getTypeUsers();
        }).catch((error) => {
            //this.errors.record(error.response.data);
            this.addLoading = false;
        });
    },
  onEditSubmit(evt) {
    editTypeUser(this.editForm).then((response) => {
      this.editLoading = false;
      this.$notify({
          title: 'Success',
          text: response.data.message,
          type: 'success'
      });
      //this.$refs['addForm'].resetFields(); // maybe not necessary
      this.editForm.type = '';
      this.editForm.id = 0;
      this.$refs.updateTypeUser.hide();
      this.getTypeUsers();
      }).catch((error) => {
        //this.errors.record(error.response.data);
        this.editLoading = false;
      });
  },
  handleDel: function (row, index) {

    this.$dialog.confirm("If you delete this record, it'll be gone forever.", {
      loader: true 
    })
      .then((dialog) => {

        this.listLoading = true;
            let params = { id: row.id };
            removeTypeUser(params).then((response) => {
            this.listLoading = false;
            this.getTypeUsers();
            this.$notify({
              title: 'Success',
              text: response.data.message,
              type: 'success'
            });
            dialog.close();
        });
 
      })
      .catch(() => {
           alert('Delete aborted');
      });

  },
  handleEdit: function (row, index) {
    this.$refs.updateTypeUser.show();
    this.editForm.type = row.type;
    this.editForm.id = row.id;
    /*this.errors = new Errors()*/
  },
  showAdd: function () {
    this.$refs.addTypeUser.show();
  },
  onFiltered(filteredItems) {
    this.totalRows = filteredItems.length;
    this.page = 1;
  }
  },
  mounted() {
    this.getTypeUsers();
  }
}
</script>


