<template>
  <div class="container">
    
    <br>
      <div class="row">
        <div class="col-md-12">
           <b-breadcrumb :items="items"/>
        </div>
      </div>
    <br>

    <div class="row">
            
      <div class="col-md-12">
                
        <b-form-group horizontal label="Filtrar" :label-cols="1">
                
          <b-form-input v-model="filter" placeholder="Buscar..." />
                
        </b-form-group>

      </div>

    </div>

    <div class="row">
        
      <div class="col-md-6">

          <b-btn variant="success" @click="showAdd">Agregar</b-btn>

      </div>
                
      <div class="col-md-6 text-md-right">

        <b-button :disabled="!sortBy" @click="sortBy = null">Limpiar busquedas</b-button>

      </div>

    </div>
      
    <br>

    <div class="row">
      
      <div class="col-md-12">
        
        <b-table striped hover show-empty
          :items="question"
          :fields="fields"
          :current-page="currentPage"
          :per-page="perPage"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          @filtered="onFiltered">
          
          <template slot="answer" slot-scope="row">
            <b-btn size="sm" @click.stop="details(row.item,row.index,$event.target)">Respuestas</b-btn>
          </template>

          <template slot="actions" slot-scope="row">
                
            <b-btn size="sm" @click.stop="handleEdit(row.item,row.index)">
                <icon name="pencil"></icon>Editar
            </b-btn>
            
            <b-btn size="sm" @click.stop="handleDel(row.item,row.index)" variant="danger">
                <icon name="trash"></icon>Eliminar</span>
            </b-btn>

          </template>
                    
        </b-table>

      </div>

    </div>

    <div class="row">

        <div class="col-md-8">

            <b-pagination :total-rows="question.length" :per-page="perPage" v-model="currentPage" />

        </div>

    </div>

    <b-modal hide-footer ref="addQuestion" title="Agregar pregunta">
      
      <b-form @submit="onSubmit">
        
        <b-form-group label-for="txtPregunta">
          
          <b-form-input   id="txtPregunta" type="text" v-model="form.question" required 
              placeholder="Ingrese pregunta">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="txtA">
          
          <b-form-input   id="txtA" type="text" v-model="res1" required
                          placeholder="Opcion A">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="txtB">
          
          <b-form-input   id="txtB" type="text" v-model="res2" required
                            placeholder="Opcion B">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="txtC">

          <b-form-input   id="txtC" type="text" v-model="res3" required
                            placeholder="Opcion C">
          </b-form-input>

        </b-form-group>

        <b-form-radio-group id="rdOpcion" v-model="IsAnswer">
           
            <b-form-radio value=0>Opcion A</b-form-radio>
            <b-form-radio value=1>Opcion B</b-form-radio>
            <b-form-radio value=2>Opcion C</b-form-radio>

        </b-form-radio-group>

        <b-button type="submit" class="btn-lg btn-block" variant="primary">Agregar</b-button>   

      </b-form>

    </b-modal>

    <b-modal hide-footer ref="updateQuestion" title="Editar usuario">
      
      <b-form @submit="onEditSubmit">
        
        <b-form-group label-for="txtPreguntaEdit">
          
          <b-form-input   id="txtPreguntaEdit" type="text" v-model="editForm.question" required 
              placeholder="Ingrese pregunta">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="txtAEdit">
          
          <b-form-input   id="txtAEdit" type="text" v-model="res1" required
                            placeholder="Opcion A">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="txtBEdit">
          
          <b-form-input   id="txtBEdit" type="text" v-model="res2" required
                            placeholder="Opcion B">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="txtCEdit">
          
          <b-form-input   id="txtCEdit" type="text" v-model="res3" required
                            placeholder="Opcion C">
          </b-form-input>

        </b-form-group>

        <b-form-radio-group id="rdOpcionEdit" v-model="IsAnswer">

            <b-form-radio value=0>Opcion A</b-form-radio>
            <b-form-radio value=1>Opcion B</b-form-radio>
            <b-form-radio value=2>Opcion C</b-form-radio>

        </b-form-radio-group> 

        <b-button type="submit" class="btn-lg btn-block" variant="primary">Submit</b-button>
      
      </b-form>

    </b-modal>

    <b-modal id="modal1" @hide="resetModal" ok-only>
      <h4 class="my-1 py-1" slot="modal-header">Index: {{ modalDetails.index }}</h4>
      <pre>{{ modalDetails.data }}</pre>
    </b-modal>

  </div>
</template>

<script>

  
  
import { getQuestionListPage, addQuestion, removeQuestion, editQuestion } from '../endpoints';
//import Errors from '../common/js/Errors';

export default {


  computed: {
  },
  props: ['trainingId'],
  data () {
    name: 'question'
    return {
      
      items: 
      [
          {
              text: 'Home',
              href: '/',
          }, 
          {
              text: 'Cursos',
              to: { name: 'CreateCurse' },
          },
          {
              text: 'Preguntas',
              active: true
          }
      ],

      IsAnswer: '',
      res1: '',
      res2: '',
      res3: '',

      answerId1: 0,
      answerId2: 0,
      answerId3: 0,

      form: {
        trainingId: this.trainingId,
        question: '',
        answer: []
      },

      fields: {
        id:         { label: 'Id', sortable: true },
        question:   { label: 'Pregunta', sortable: true },
        trainingId: { label: 'Id del curso'},
        created_at: { label: 'Fecha de creaci√≥n' },
        updated_at: { label: 'Fecha de actualizaci√≥n' },
        answer:     { label: 'Respuestas' },      
        actions:    { label: 'Acciones' }
       },

      question: [],
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      sortBy: null,
      sortDesc: false,
      filter: null,
      listLoading: false,
      addFormVisible: false,
      addLoading: false,
      editFormVisible: false,
      editLoading: false,
      addForm: {},
      editForm: {
        id: 0,
        trainingId: this.trainingId,
        question: '',
        answer: []
      },
      modalDetails: { index:'', data:'' }
    }
  },
  methods: {

    getQuestion() {

      this.listLoading = true;
      let params = { trainingId: this.trainingId };
      getQuestionListPage(params).then((res) => {
        this.question = res.data;
        this.listLoading = false;
      });

    },

    onSubmit(evt) {
      
      this.addLoading = true;

      let respuesta1 = {questionId: 0, answer: this.res1, isAnswer: this.IsAnswer == 0 ? true : false};  
      this.form.answer.push(respuesta1);
      let respuesta2 = {questionId: 0, answer: this.res2, isAnswer: this.IsAnswer == 1 ? true : false};  
      this.form.answer.push(respuesta2);
      let respuesta3 = {questionId: 0, answer: this.res3, isAnswer: this.IsAnswer == 2 ? true : false};  
      this.form.answer.push(respuesta3);

        addQuestion(this.form).then((response) => {
            this.addLoading = false;
            this.$notify({
              title: 'Success',
              text: response.data.message,
              type: 'success'
            });
            this.form.question = '';
            this.res1 = '';
            this.res1 = '';
            this.res1 = '';
            this.$refs.addQuestion.hide();
            evt.target.reset();
            this.addFormVisible = false;
            this.getQuestion();
        }).catch((error) => {
            this.addLoading = false;
        });
    },

    onEditSubmit(evt) {
      
      this.editLoading = true;  

      let respuesta1 = {id: this.answerId1, questionId: 0, answer: this.res1, isAnswer: this.IsAnswer == 0 ? true : false};  
      this.editForm.answer.push(respuesta1);
      
      let respuesta2 = {id:  this.answerId2, questionId: 0, answer: this.res2, isAnswer: this.IsAnswer == 1 ? true : false};  
      this.editForm.answer.push(respuesta2);
      
      let respuesta3 = {id:  this.answerId3, questionId: 0, answer: this.res3, isAnswer: this.IsAnswer == 2 ? true : false};  
      this.editForm.answer.push(respuesta3);

      editQuestion(this.editForm).then((response) => {
        this.editLoading = false;
        
        this.$notify({
          title: 'Success',
          text: response.data.message,
          type: 'success'
        });

        this.editForm.question = '';
        this.res1 = '';
        this.res1 = '';
        this.res1 = '';
        this.answerId1 = 0;
        this.answerId2 = 0;
        this.answerId3 = 0;

        this.editForm.id = 0;
        this.$refs.updateQuestion.hide();
        evt.target.reset();
        this.getQuestion();
      }).catch((error) => {
          this.editLoading = false;
      });
    },

    handleDel: function (row, index) {

      this.$dialog.confirm("If you delete this record, it'll be gone forever.", {
        loader: true 
      })
        .then((dialog) => {

          this.listLoading = true;
              let params = { id: row.id };
              removeQuestion(params).then((response) => {
              this.listLoading = false;
              this.getQuestion();
              this.$notify({
                title: 'Success',
                text: response.data.message,
                type: 'success'
              });
              dialog.close();
          });
   
        })
        .catch(() => {
             alert('Delete aborted');
        });

    },
    handleEdit: function (row, index) {
      this.$refs.updateQuestion.show();
      this.editForm.question = row.question;
      this.editForm.id = row.id;
      this.res1 = row.answer[0].answer;
      this.res2 = row.answer[1].answer;
      this.res3 = row.answer[2].answer;

      this.answerId1 = row.answer[0].id;
      this.answerId2 = row.answer[1].id;
      this.answerId3 = row.answer[2].id;

      
    },

    showAdd: function () {
      this.$refs.addQuestion.show();
    },

    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.page = 1;
    },

    details(item, index, button) {
      this.modalDetails.data = JSON.stringify(item.answer, null, 2);
      this.modalDetails.index = index;
      this.$root.$emit('bv::show::modal','modal1', button);
    },

  },
  mounted() {
    this.getQuestion();
  }
}
</script>


