<template>
	
    <div class="container">
		
        <br>
	    <div class="row">
	      <div class="col-md-12">
	         <b-breadcrumb :items="items"/>
	      </div>
	    </div>
	    <br>
        
        <div class="row">
            
            <div class="col-md-12">
                
                <b-form-group horizontal label="Filtrar" :label-cols="1">
                
                    <b-form-input v-model="filter" placeholder="Buscar..." />
                
                </b-form-group>

           </div>

        </div>

     	<div class="row">
     		
            <div class="col-md-6">
     			<b-btn variant="success" @click="showAdd">Agregar</b-btn>
     		</div>
                
            <div class="col-md-6 text-md-right">
                <b-button :disabled="!sortBy" @click="sortBy = null">Limpiar busquedas</b-button>
            </div>

     	</div>
     	
        <br>

		<div class="row">
			<div class="col-md-12">
                <b-table striped hover show-empty
                    :items="theory"
                    :fields="rows"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    @filtered="onFiltered">
                    
                    <template slot="actions" slot-scope="row">
                
                        <b-btn size="sm" @click.stop="handleEdit(row.item,row.index)">
                            <icon name="pencil"></icon>Editar
                        </b-btn>
                        
                        <b-btn size="sm" @click.stop="handleDel(row.item,row.index)" variant="danger">
                            <icon name="trash"></icon>Eliminar</span>
                        </b-btn>

                    </template>
                    
                </b-table>
			</div>
		</div>

        <div class="row">
            <div class="col-md-8">
                <b-pagination :total-rows="theory.length" :per-page="perPage" v-model="currentPage" />
            </div>
        </div>
        
        <b-modal hide-footer ref="addTheory" title="Agregar Material">
                
            <b-form @submit="onSubmit">
            
                <b-form-group   id="descriptionIn"
                                label-for="description">
                    <b-form-input   id="description"
                                    type="text" v-model="form.description" required
                                    placeholder="Ingrese la descripci√≥n del curso">
                    </b-form-input>

                </b-form-group>

                <b-form-group  id="url"
                                label-for="Url">
                    <b-form-input   id="Url"
                                    type="text" v-model="form.url" required
                                    placeholder="Ingrese la descripci√≥n del curso">
                    </b-form-input>

                </b-form-group>
                
                <br />
                <b-button type="submit" class="btn-lg btn-block" variant="primary">Agregar</b-button>
          
            </b-form>

        </b-modal>

        <b-modal hide-footer ref="updateTheory" title="Editar Material">
          
            <b-form @submit="onEditSubmit">
          
                 <b-form-group  id="editDescriptionIn"
                                label-for="editDescription">
                    <b-form-input   id="editDescription"
                                    type="text" v-model="editForm.description" required
                                    placeholder="Ingrese la descripci√≥n del curso">
                    </b-form-input>

                </b-form-group>

                <b-form-group  id="editUrl"
                                label-for="editurl">
                    <b-form-input   id="editurl"
                                    type="text" v-model="editForm.url" required
                                    placeholder="Ingrese la liga del archivo">
                    </b-form-input>

                </b-form-group>
                
                <br />          
                <b-button type="submit" class="btn-lg btn-block" variant="primary">Editar</b-button>
      
            </b-form>

        </b-modal>
        
	</div>
</template>

<script>

import { getTheoryListPage, addTheory, removeTheory, editTheory } from '../endpoints';

export default {

  computed: {
  },
  props: ['trainingId'],
  data () {
    name: 'theory'
        
        return {
            
            items: 
            [
                {
                    text: 'Home',
                    href: '/',
                }, 
                {
                    text: 'Cursos',
                    to: { name: 'CreateCurse' },
                },
                {
                    text: 'Materiales',
                    active: true
                }
            ],

            form: {
                trainingId: this.trainingId,
                description: '',
            },

            rows: {
                id:             { label: 'Id', sortable: true },
                url:            { label: 'Liga', sortable: true  },
                description:    { label: 'Descripci√≥n', sortable: true  },
                created_at:     { label: 'Fecha de creaci√≥n' },
                updated_at:     { label: 'Fecha de actualizaci√≥n' },
                actions:        { label: 'Acciones' }
            },
    
            theory: [],
            currentPage: 1,
            perPage: 5,
            totalRows: 0,
            sortBy: null,
            sortDesc: false,
            filter: null,
            listLoading: false,
            addFormVisible: false,
            addLoading: false,
            editFormVisible: false,
            editLoading: false,
            addForm: {},
            editForm: {
                id: 0,
                trainingId: this.trainingId,
                description: '',
            }
        }
    },
    methods: {
    
        getTheory() {
            
            this.listLoading = true;
            let params = { trainingId: this.trainingId };
            getTheoryListPage(params).then((res) => {
                this.theory = res.data;
                this.listLoading = false;
            });
        },

        onSubmit(evt) {
            this.addLoading = true;
            addTheory(this.form).then((response) => {
                this.addLoading = false;
                this.$notify({
                  title: 'Success',
                  text: response.data.message,
                  type: 'success'
                });
                this.form.descripci√≥n = '';
                this.form.id = '';
                this.$refs.addTheory.hide();
                this.addFormVisible = false;
                evt.target.reset();
                this.getTheory();
            }).catch((error) => {
                this.addLoading = false;
            });
        },

      onEditSubmit(evt) {
        editTheory(this.editForm).then((response) => {
            this.editLoading = false;
            this.$notify({
              title: 'Success',
              text: response.data.message,
              type: 'success'
            });
            this.editForm.descripci√≥n = '';
            this.editForm.id = 0;
            this.$refs.updateTheory.hide();
            evt.target.reset();
            this.getTheory();
            }).catch((error) => {
                this.editLoading = false;
            });
      },
      handleDel: function (row, index) {

        this.$dialog.confirm("If you delete this record, it'll be gone forever.", {
          loader: true 
        })
          .then((dialog) => {

            this.listLoading = true;
                let params = { id: row.id };
                removeTheory(params).then((response) => {
                this.listLoading = false;
                this.getTheory();
                this.$notify({
                  title: 'Success',
                  text: response.data.message,
                  type: 'success'
                });
                dialog.close();
            });
     
          })
          .catch(() => {
               alert('Delete aborted');
          });

        },

        handleEdit: function (row, index) {
            this.$refs.updateTheory.show();
            this.editForm.descripci√≥n = row.description;
            this.editForm.url = row.url;
            this.editForm.id = row.id;
        },

        showAdd: function () {
            this.$refs.addTheory.show();
        },

        onFiltered(filteredItems) {
            this.totalRows = filteredItems.length;
            this.page = 1;
        }
    },
    mounted() {
        this.getTheory();
    }
}
</script>


