import Vue from 'vue'
import VeeValidate from 'vee-validate'
import Router from 'vue-router'
import Home from '@/components/Home'
import Kardex from '@/components/Kardex'
import Curso from '@/components/Curso'
import UserCourse from '@/components/UserCourse'
import CreateCurse from '@/components/CreateCurse'
import NotFound from '@/components/404'
import Login from '@/components/Login'
import Theory from '@/components/Theory'
import Users from '@/components/Users'
import TypeUser from '@/components/TypeUser'
import Question from '@/components/Question'

Vue.use(VeeValidate)
Vue.use(Router)

export default new Router({
  routes: [
    {
  		path: '/login',
	  	name: 'Login',
	  	hidden: true,
	  	component: Login,
	  	meta: {auth: false},
    },
    {
  		path: '/',
	  	name: 'Home',
	  	component: Home,
	  	meta: {auth: true},
    },
    {
  		path: '/users',
	  	name: 'Users',
	  	component: Users,
	  	meta: {auth: true},
    },
    {
  		path: '/typeUser',
	  	name: 'TypeUser',
	  	component: TypeUser,
	  	meta: {auth: true},
    },
    {
  		path: '/theory/:trainingId',
	  	name: 'Theory',
	  	component: Theory,
	  	props: true,
	  	meta: {auth: true},
    },
    {
  		path: '/question/:trainingId',
	  	name: 'Question',
	  	component: Question,
	  	props: true,
	  	meta: {auth: true},
    },
    {
  		path: '/createCurse',
	  	name: 'CreateCurse',
	  	component: CreateCurse,
	  	meta: {auth: true},
    },

    {
  		path: '/kardex',
	  	name: 'Kardex',
	  	component: Kardex,
	  	meta: {auth: true},
    },
    {
  		path: '/curso',
	  	name: 'Curso',
	  	component: Curso,
	  	meta: {auth: true},
    },
    {
  		path: '/UserCourse',
	  	name: 'UserCourse',
	  	component: UserCourse,
	  	meta: {auth: true},
    },
    {
		path: '/404',
	    component: NotFound,
	    name: '',
	    hidden: true
    },
    {
	    path: '*',
	    hidden: true,
	    redirect: { path: '/404' }
    }
  ]
})



