<template>
	
    <div class="container">
		
        <br>
	    <div class="row">
	      <div class="col-md-12">
	         <b-breadcrumb :items="items"/>
	      </div>
	    </div>
	    <br>
        
        <div class="row">
            
            <div class="col-md-12">
                
                <b-form-group horizontal label="Filtrar" :label-cols="1">
                
                    <b-form-input v-model="filter" placeholder="Buscar..." />
                
                </b-form-group>

           </div>

        </div>

        <div class="row">
            
            <div class="col-md-6">
                <b-btn variant="success" @click="showAdd">Agregar</b-btn>
            </div>
                
            <div class="col-md-6 text-md-right">
                <b-button :disabled="!sortBy" @click="sortBy = null">Limpiar busquedas</b-button>
            </div>

        </div>
     	
        <br>

		<div class="row">
			<div class="col-md-12">
                <b-table striped hover show-empty
                    :items="training"
                    :fields="rows"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    @filtered="onFiltered">
                    
                    <template slot="user" slot-scope="row">
                        
                        <b-btn size="sm" @click.stop="handleUserCourse(row.item,row.index)" variant="primary">
                            <icon></icon>Agregar usuario
                        </b-btn>

                    </template>
                    
                    <template slot="material" slot-scope="row">
                        
                        <b-btn size="sm" @click.stop="handleTheory(row.item,row.index)" variant="primary">
                            <icon></icon>Material
                        </b-btn>

                    </template>

                    <template slot="question" slot-scope="row">
                        
                        <b-btn size="sm" @click.stop="handleQuestion(row.item,row.index)" variant="primary">
                            <icon></icon>Preguntas
                        </b-btn>

                    </template>

                    <template slot="actions" slot-scope="row">
                
                        <b-btn size="sm" @click.stop="handleEdit(row.item,row.index)">
                            <icon name="pencil"></icon>Editar
                        </b-btn>
                        
                        <b-btn size="sm" @click.stop="handleDel(row.item,row.index)" variant="danger">
                            <icon name="trash"></icon>Eliminar</span>
                        </b-btn>

                    </template>
                    
                </b-table>
			</div>
		</div>

        <div class="row">
            <div class="col-md-8">
                <b-pagination :total-rows="training.length" :per-page="perPage" v-model="currentPage" />
            </div>
        </div>
        
        <b-modal hide-footer ref="addTraining" title="Agregar Curso">
                
            <b-form @submit="onSubmit">
            
                <b-form-group   id="labelCurse"
                                label-for="curse">
                    <b-form-input   id="curse"
                                    type="text" v-model="form.name" required
                                    placeholder="Ingrese el nombre del curso">
                    </b-form-input>

                </b-form-group>

                <b-button type="submit" class="btn-lg btn-block" variant="primary">Agregar</b-button>
          
            </b-form>

        </b-modal>

        <b-modal hide-footer ref="updateTraining" title="Editar Curso">
          
            <b-form @submit="onEditSubmit">
          
                <b-form-group   id="editCurse"
                                label-for="EditCurse">
                    <b-form-input   id="EditCurse"
                                    type="text" v-model="editForm.name" required
                                    placeholder="Ingrese nombre del curso">
                    </b-form-input>

                </b-form-group>
          
                <b-button type="submit" class="btn-lg btn-block" variant="primary">Editar</b-button>
      
            </b-form>

        </b-modal>
    
        <b-modal ref="addUCourse" @hide="resetForm" hide-footer title="Agregar Usuario Curso">
            
                <b-form @submit="onLoadUser" id="AddUserCourse"> 

                    <b-form-group   label-for="userCourse">
              
                        <b-form-select v-model="userCurso.UserId" :options="options" class="mb-3"></b-form-select>

                    </b-form-group>

                    <b-button type="submit" class="btn-lg btn-block" variant="primary">Agregar</b-button>

                </b-form>
        </b-modal>

	</div>
</template>

<script>

import { addUserCourse ,getUserListPage ,getTrainingListPage, addTraining, removeTraining, editTraining } from '../endpoints';

export default {

  computed: {
  },

  data () {
    name: 'training'
        
        return {
            
            items: 
            [
                {
                    text: 'Home',
                    href: '/',
                }, 
                {
                    text: 'Cursos',
                    active: true
                }
            ],

            form: {
                name: '',
            },

            rows: {
                id:         { label: 'Id', sortable: true },
                name:       { label: 'Nombre', sortable: true  },
                created_at: { label: 'Fecha de creaci√≥n' },
                updated_at: { label: 'Fecha de actualizaci√≥n' },
                question:   { label: 'Ver preguntas' },
                user:       { label: 'Agregar usuarios' },
                material:   { label: 'Ver material' },
                actions:    { label: 'Acciones' }
            },
            users: [],
            training: [],
            currentPage: 1,
            perPage: 5,
            totalRows: 0,
            sortBy: null,
            sortDesc: false,
            filter: null,
            listLoading: false,
            addFormVisible: false,
            addLoading: false,
            editFormVisible: false,
            editLoading: false,
            addForm: {},
            
            editForm: {
                id: 0,
                name: ''
            },
            options: [{ value: null, text: 'Seleccione una opcion' }],    
            userCurso: {
                UserId: null,
                trainingId : 0,
                correctAnswers: 0,
                wrongAnswers: 0
            }
        }
    },
    methods: {
    
        getTraining() {
            
            this.listLoading = true;
            
            getTrainingListPage().then((res) => {
                this.training = res.data;
                this.listLoading = false;
            });
        },

        getUsers() {
            this.listLoading = true;
            getUserListPage().then((res) => {
                this.users = res.data;
                this.users.forEach(function(element) {
                  this.options.push({ value: element.id, text: element.name });
                },this);
                this.listLoading = false;
            });
        },  

        onSubmit(evt) {
            this.addLoading = true;
            addTraining(this.form).then((response) => {
                this.addLoading = false;
                this.$notify({
                  title: 'Success',
                  text: response.data.message,
                  type: 'success'
                });
                this.form.name = '';
                this.$refs.addTraining.hide();
                evt.target.reset();
                this.addFormVisible = false;
                this.getTraining();
            }).catch((error) => {
                this.addLoading = false;
            });
        },

        onLoadUser(evt) {
            this.addLoading = true;
            addUserCourse(this.userCurso).then((response) => {
                this.addLoading = false;
                this.$notify({
                  title: 'Success',
                  text: response.data.message,
                  type: 'success'
                });
                this.userCurso.UserId = null;
                this.userCurso.trainingId = 0;
                this.$refs.addUCourse.hide();
                evt.target.reset();
                this.addFormVisible = false;
            }).catch((error) => {
                this.addLoading = false;
            });
        },

        onEditSubmit(evt) {
            editTraining(this.editForm).then((response) => {
                this.editLoading = false;
                this.$notify({
                    title: 'Success',
                    text: response.data.message,
                    type: 'success'
                });
                this.editForm.name = '';
                this.editForm.id = 0;
                this.$refs.updateTraining.hide();
                evt.target.reset();
                this.getTraining();
                }).catch((error) => {
                this.editLoading = false;
                });
        },



        handleDel: function (row, index) {

            this.$dialog.confirm("If you delete this record, it'll be gone forever.", {
            loader: true 
            })
            .then((dialog) => {

                this.listLoading = true;
                    let params = { id: row.id };
                    removeTraining(params).then((response) => {
                        this.listLoading = false;
                        this.getTraining();
                        this.$notify({
                            title: 'Success',
                            text: response.data.message,
                            type: 'success'
                        });
                        dialog.close();
                    });
     
            })
            .catch(() => {
                alert('Delete aborted');
            });

        },

        handleEdit: function (row, index) {
            this.$refs.updateTraining.show();
            this.editForm.type = row.type;
            this.editForm.id = row.id;
        },

        handleUserCourse: function (row, index) {
            this.getUsers();
            this.$refs.addUCourse.show();
            this.userCurso.trainingId = row.id;
        },

        showAdd: function () {
            this.$refs.addTraining.show();
        },

        handleTheory: function (row, index) {
            let dir = 'theory/' + row.id;
            this.$router.push({ path: dir });
        },

        handleQuestion: function (row, index) {
            let dir = 'question/' + row.id;
            this.$router.push({ path: dir });
        },

        onFiltered(filteredItems) {
            this.totalRows = filteredItems.length;
            this.page = 1;
        },

        resetForm(form)
        {
            document.getElementById("AddUserCourse").reset(); 
            this.options = [{ value: null, text: 'Seleccione un valor' }];
            this.userCurso.UserId = null;
            this.editForm.trainingId = 0;
        }

    },
    mounted() {
        this.getTraining();
    }
}
</script>


