<template>

  <div class="container">
    
    <br>
      <div class="row">
        <div class="col-md-12">
           <b-breadcrumb :items="items"/>
        </div>
      </div>
    <br>

    <div class="row">
            
      <div class="col-md-12">
          
          <b-form-group horizontal label="Filtrar" :label-cols="1">
          
              <b-form-input v-model="filter" placeholder="Buscar..." />
          
          </b-form-group>

      </div>

    </div>

    <div class="row">
      
      <div class="col-md-6">
        <b-btn variant="success" @click="showAdd">Agregar</b-btn>
      </div>
              
      <div class="col-md-6 text-md-right">
        <b-button :disabled="!sortBy" @click="sortBy = null">Limpiar busquedas</b-button>
      </div>

    </div>
    
    <br />

    <div class="row">
      
      <div class="col-md-12">
        
        <b-table striped hover show-empty
            :items="user"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            @filtered="onFiltered">       
          
          <template slot="sex" slot-scope="data">
            {{ data.value == 0  ? 'Femenino' : 'Masculino' }}
          </template>
          
          <template slot="type_user" slot-scope="data">
            {{data.value.type}}
          </template>

          <template slot="actions" slot-scope="row">            
          
            <b-btn size="sm" @click.stop="handleEdit(row.item,row.index)">
              <icon name="pencil"></icon>Editar
            </b-btn>          
          
            <b-btn size="sm" @click.stop="handleDel(row.item,row.index)" variant="danger">
              <icon name="trash"></icon>Eliminar</span>
            </b-btn>

          </template>

        </b-table> 

      </div> 

    </div>
    
    <div class="row">

      <div class="col-sm-8">

              <b-pagination :total-rows="user.length" :per-page="perPage" v-model="currentPage" />
      
      </div>
    
    </div>


    <b-modal hide-footer ref="addUser" title="Agregar usuario" @hide="resetForm('AddForm')">
      
      <b-form @submit="onSubmit" id="AddForm">
        
        <b-form-group label-for="userName">
          
          <b-form-input   id="userName" type="text" v-model="form.name" required 
                          placeholder="Ingrese el nombre del usuario">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="User">
          
          <b-form-select v-model="form.typeUserId" :options="options" class="mb-3"></b-form-select>

        </b-form-group>

        <b-form-group   label-for="usrEmail">
         
          <b-form-input     id="usrEmail" type="text" v-model="form.email" required
                            placeholder="Ingrese el email del usuario">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="usrPassword">
          
          <b-form-input     id="usrPassword" type="password" v-model="form.password" required
                            placeholder="Ingrese la contrase√±a del usuario">
          </b-form-input>

        </b-form-group>

        <b-form-radio-group  v-model="form.sex" label="ASDASD">
            
            <b-form-radio value="1">Masculino</b-form-radio>
            <b-form-radio value="0">Femenino</b-form-radio>

        </b-form-radio-group>

        <b-form-group   label-for="usrDate">
          
          <b-form-input     id="usrDate" type="text" v-model="form.birth" required
                            placeholder="Ingrese fecha de nacimiento">
          </b-form-input>

        </b-form-group>

        <b-button type="submit" class="btn-lg btn-block" variant="primary">Agregar</b-button> 

      </b-form>

    </b-modal>

    <b-modal hide-footer ref="updateUser" title="Editar usuario" @hide="resetForm('EditFormHandle')">
      
      <b-form @submit="onEditSubmit" id='EditFormHandle'>

        <b-form-group label-for="editUserName">
          
          <b-form-input   id="editUserName" type="text" v-model="editForm.name" required 
                          placeholder="Ingrese el nombre del usuario">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="editTypeUser">
          
          <b-form-select v-model="editForm.typeUserId" :options="options" class="mb-3"></b-form-select>

        </b-form-group>

        <b-form-group   label-for="editUsrEmail">
         
          <b-form-input   id="editUsrEmail" type="text" v-model="editForm.email" required
                            placeholder="Ingrese el email del usuario">
          </b-form-input>

        </b-form-group>

        <b-form-group   label-for="editUsrPassword">
          
          <b-form-input   id="editUsrPassword" type="text" v-model="editForm.password" required
                            placeholder="Ingrese la contrase√±a del usuario">
          </b-form-input>

        </b-form-group>

        <b-form-radio-group v-model="editForm.sex">
            
            <b-form-radio value="1">Masculino</b-form-radio>
            <b-form-radio value="0">Femenino</b-form-radio>

        </b-form-radio-group>

        <b-form-group   label-for="editUsrDate">
          
          <b-form-input   id="editUsrDate" type="text" v-model="editForm.birth" required
                            placeholder="Ingrese fecha de nacimiento">
          </b-form-input>

        </b-form-group> 

          <b-button type="submit" class="btn-lg btn-block" variant="primary">Submit</b-button>
      
      </b-form>

    </b-modal>
    


  </div>

</template>

<script>

  
  
import { getTypeUserListPage, getUserListPage, addUser, removeUser, editUser } from '../endpoints';
//import Errors from '../common/js/Errors';

export default {


  computed: {
  },

  data () {
    name: 'users'
    
    return {
      
      items: 
      [
          {
              text: 'Home',
              href: '/',
          }, 
          {
              text: 'Usuarios',
              active: true
          }
      ], 

      form: {
        typeUserId: null,
        name: '',
        email: '',
        password: '',
        sex: 0,
        birth: '',  
      },

      fields: {
        id:           { label: 'Id', sortable: true },
        name:         { label: 'Nombre',sortable: true },
        type_user:    { label: 'Tipo', sortable: true  },
        email:        { label: 'Email',sortable: true },
        sex:          { label: 'Sexo',sortable: true  },
        birth:        { label: 'Fecha de nacimiento',sortable: true  },
        created_at:   { label: 'Fecha de creaci√≥n' },
        updated_at:   { label: 'Fecha de actualizaci√≥n' },
        actions:      { label: 'Acciones' }
       },

      pageOptions: [{text:5,value:5},{text:10,value:10},{text:15,value:15}],
    
      user: [],
      typeUser: [],
      options: [{ value: null, text: 'Please select an option' }],
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      sortBy: null,
      sortDesc: false,
      filter: null,
      listLoading: false,
      addFormVisible: false,
      addLoading: false,
      editFormVisible: false,
      editLoading: false,
      addForm: {},
      
      editForm: {
        id: 0,
        typeUserId: 0,
        name: '',
        email: '',
        password: '',
        sex: 0,
        birth: '',  
      }

    }
  },

  methods: {
    
    getUsers() {
      this.listLoading = true;
      getUserListPage().then((res) => {
        this.user = res.data;
        this.listLoading = false;
      });
    },

    getTypeUsers() {
      this.listLoading = true;
      getTypeUserListPage().then((res) => {
        this.typeUser = res.data;
        this.typeUser.forEach(function(element) {
          this.options.push({ value: element.id, text: element.type });
        },this);
        this.listLoading = false;
      });
    },  

    onSubmit(evt) {
      this.addLoading = true;
        addUser(this.form).then((response) => {
            this.addLoading = false;
            this.$notify({
              title: 'Success',
              text: response.data.message,
              type: 'success'
            });
            this.form.name = '';
            this.form.email = '';
            this.form.sex = 0;
            this.form.birth = '';
            this.form.typeUserId = null;
            this.form.password = '';
            this.form.id = 0;
            evt.target.reset();
            this.$refs.addUser.hide();
            this.addFormVisible = false;
            this.getUsers();
        }).catch((error) => {
            this.addLoading = false;
        });
    },
  onEditSubmit(evt) {
    editUser(this.editForm).then((response) => {
      this.editLoading = false;
      this.$notify({
          title: 'Success',
          text: response.data.message,
          type: 'success'
      });
      this.editForm.name = '';
      this.editForm.email = '';
      this.editForm.sex = 0;
      this.editForm.birth = '';
      this.editForm.typeUserId = null;
      this.editForm.password = '';
      this.editForm.id = 0;
      evt.target.reset();
      this.$refs.updateUser.hide();
      this.getUsers();
      }).catch((error) => {
        this.editLoading = false;
      });
  },
  handleDel: function (row, index) {

    this.$dialog.confirm("If you delete this record, it'll be gone forever.", {
      loader: true 
    })
      .then((dialog) => {

        this.listLoading = true;
            let params = { id: row.id };
            removeUser(params).then((response) => {
            this.listLoading = false;
            this.getUsers();
            this.$notify({
              title: 'Success',
              text: response.data.message,
              type: 'success'
            });
            dialog.close();
        });
 
      })
      .catch(() => {
           alert('Delete aborted');
      });

  },
  handleEdit: function (row, index) {
    this.getTypeUsers();
    this.$refs.updateUser.show();
    this.editForm.name = row.name;
    this.editForm.email = row.email;
    this.editForm.sex = row.sex == 'Femenino'  ? 0 : 1;
    this.editForm.birth = row.birth;
    this.editForm.typeUserId = row.type_user.id;
    this.editForm.password = '';
    this.editForm.id = row.id;
    /*this.errors = new Errors()*/
  },
  showAdd: function () {
    this.getTypeUsers();
    this.$refs.addUser.show();
  },
  onFiltered(filteredItems) {
    this.totalRows = filteredItems.length;
    this.page = 1;
  },
  resetForm(form)
  {
    document.getElementById("AddForm").reset(); 
    document.getElementById("EditFormHandle").reset(); 
    this.options = [{ value: null, text: 'Seleccione un valor' }];
    this.form.typeUserId = null;
    this.editForm.typeUserId = null;
  }
  },
  mounted() {
    this.getUsers();
  }
}
</script>


