<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h1>{{NombreCurso}}</h1>
      </div>
    </div>
    <br>
    <h2>Material</h2>
    <div class="row">
      <div class="col-md-12">
        <template>
          <b-table striped hover :items="material"></b-table>
        </template>
      </div>
    </div>
     <br>
    <h2>Preguntas</h2>
    <div v-for="Pregunta in Preguntas">
      <div class="row">
        <div class="col-md-12">
          <p>{{Pregunta}}</p>
          <b-form-radio-group id="radios2" v-model="selected" name="radioSubComponent">
            <b-form-radio value="A">Resp 1</b-form-radio><br>
            <b-form-radio value="B">ASD</b-form-radio><br>
            <b-form-radio value="C" >QWE</b-form-radio>          
          </b-form-radio-group>
        </div>
      </div>
    </div>
    <br>
    <b-button variant="success">Guardar</b-button>
  </div>
</template>

<script>
const Preguntas = [
  { question: 'curso1',A:"Resp",B:"asd",C:"qwe"},
  { question: 'curso2',A:"Resp",B:"aasdsd",C:"qwe"},
  { question: 'curso3',A:"Resp",B:"asd",C:"qqweqwwe"},
  { question: 'curso4',A:"Resp",B:"aertsd",C:"qerterwe"}
];
const material = [
  { Nombre: 'material1',Tipo: 'video',Liga :"youtube"},
  { Nombre: 'material1',Tipo: 'video',Liga :"youtube"},
  { Nombre: 'material1',Tipo: 'video',Liga :"youtube"},
  { Nombre: 'material1',Tipo: 'video',Liga :"youtube"}
];

export default {
  data() {
    return {
      NombreCurso: "Curso1",
      material: material,
      Preguntas: Preguntas
    }
  }
}
</script>


